* {
  margin: 0px;
  padding: 0px;
  color: #ADADAD;
  list-style-type: none;
  text-decoration: none; }

body {
  height: 100vh;
  background: #F9F9F9; }

.header {
  display: flex;
  flex-wrap: wrap;
  border-bottom: ridge 1px #DDD;
  background-color: white;
  box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.05), 0 2px 4px 0 rgba(0, 0, 0, 0.06); }
  .header .header-inner {
    display: flex;
    flex-grow: 1;
    flex-wrap: wrap;
    justify-content: center; }
    .header .header-inner .headertop {
      display: flex;
      flex-basis: 80%;
      align-items: center;
      width: 80%; }
      .header .header-inner .headertop .logo {
        display: inline-flex;
        flex-grow: 1;
        transform: scale(0.7); }
      .header .header-inner .headertop .header--search-box {
        display: flex;
        flex-grow: 100;
        padding: 6px;
        border: solid 1px #DDD; }
        .header .header-inner .headertop .header--search-box .header--search-icon {
          margin: 10px; }
        .header .header-inner .headertop .header--search-box #header--input-search {
          margin-left: 8px;
          width: 100%;
          border-style: none;
          color: #ADADAD;
          font-style: normal;
          font-size: 0.65em; }
      .header .header-inner .headertop .header--dd-menu-main-btn {
        display: flex;
        flex-grow: 1;
        padding-left: 8px; }
      .header .header-inner .headertop .header--dd-menu {
        display: none; }
      .header .header-inner .headertop .header--btn-login {
        display: flex;
        flex-grow: 1;
        margin-left: 6px;
        color: #ADADAD;
        font-style: normal;
        font-size: 0.65em; }
    .header .header-inner .headerbttm {
      display: flex;
      justify-content: center;
      width: 80%; }
      .header .header-inner .headerbttm .header--mainnav {
        display: flex;
        flex-grow: 1;
        border-top: solid 1px #DDD; }
        .header .header-inner .headerbttm .header--mainnav .main-nav-btns {
          display: flex;
          flex-grow: 1;
          min-height: 5vh; }
          .header .header-inner .headerbttm .header--mainnav .main-nav-btns .nav-btn {
            display: flex;
            align-items: center;
            justify-content: center;
            min-width: 10vw;
            height: 100%;
            color: #ADADAD;
            font-style: normal;
            font-size: 0.9em; }
          .header .header-inner .headerbttm .header--mainnav .main-nav-btns .nav-btn:hover {
            background: #F9F9F9; }
          .header .header-inner .headerbttm .header--mainnav .main-nav-btns .nav-btn1 {
            padding-right: 6px; }

.contents--container .contents {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  margin: auto;
  width: 80%; }
  .contents--container .contents .welcome {
    display: flex;
    flex-grow: 1;
    flex-basis: 100%;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding-top: 1vh;
    padding-right: 2vw;
    padding-bottom: 1vh;
    padding-left: 2vw;
    height: 8vh;
    background-color: white; }
    .contents--container .contents .welcome * {
      margin-left: 10px; }
    .contents--container .contents .welcome .welcome--logo {
      transform: scale(0.7); }
    .contents--container .contents .welcome .welcome--msg {
      color: black;
      font-style: bold;
      font-size: 0.7em; }
    .contents--container .contents .welcome .welcome--subscribe-msg {
      color: #ADADAD;
      font-style: normal;
      font-size: 0.65em; }
    .contents--container .contents .welcome #welcome--email {
      padding: 5px;
      color: #ADADAD;
      font-style: normal;
      font-size: 0.65em; }
    .contents--container .contents .welcome #welcome--submit {
      display: inline-flex;
      padding: 4px;
      min-width: 8vw;
      max-width: 8vw;
      border-style: none;
      border-radius: 4px;
      background: #DA552F;
      color: white; }
      .contents--container .contents .welcome #welcome--submit:hover {
        background-color: #DA7937; }
  .contents--container .contents .contents--columns {
    display: flex;
    justify-content: space-between; }
    .contents--container .contents .contents--columns .column1 {
      display: flex;
      flex-grow: 2;
      margin-right: 1vw;
      height: 10vh; }
    .contents--container .contents .contents--columns .column2 {
      display: flex;
      flex-grow: 1;
      margin-left: 1vw; }
      .contents--container .contents .contents--columns .column2 .kitbit {
        display: flex;
        height: 10vh; }
